@import url(https://fonts.googleapis.com/css2?family=Montserrat&family=Poppins:wght@400;700&display=swap);
body{
    background: linear-gradient(0.25turn,#EDC1D3, #91BDEC);
    display: flex;
    align-items: center;
    justify-content: center;
}

/*Container Start*/

.container{
    max-width: 80vw;
    margin: 50px 0;
    background-color: #fff;
    border-radius: 30px;
    box-shadow: 0px 5px 10px 2px rgba(34, 60, 80, 0.2);
    padding: 90px 71px;
}

/*Container End*/

/*Header Start*/

.header{
    display: flex;
    align-items: center;
    grid-column-gap: 26px;
    -webkit-column-gap: 26px;
            column-gap: 26px;
    margin-bottom: 41px;
}

.title__pink{
    color: #F3477A;
}

.header__button{
    display: flex;
    justify-content: center;
    align-items: center;
    grid-column-gap: 5px;
    -webkit-column-gap: 5px;
            column-gap: 5px;
    width: 140px;
    height: 35px;
    border: none;
    border-radius: 10px;
    background-color: #884CB2;
    color: #fff;
    cursor: pointer;
}

.header__button:hover{
    background-color: #6FCF97;
    border: 1px solid #90A0B7;
    transition: background-color 0.2s;
}

/*Header End*/

/*OnProcces Start*/

.onproccess{
    margin-bottom: 50px;
}

.onproccess__title{
    margin-bottom: 28px;
    font-size: 18px;
    line-height: 27px;
}

.todoItem{
    display: flex;
    align-items: center;
    padding: 20px 0;
    border-bottom: 1px solid #E0E0E0;
}

.todoItem__text{
    margin-left: 10px;
    margin-right: 30px;
    font-family: Montserrat;
    font-size: 14px;
    line-height: 17px;
}

.todoItem__done {
    width: 25px;
    margin-right: 10px;
    cursor: pointer;
}

.todoItem__delete {
    width: 25px;
    cursor: pointer;
}

/*OnProccess End*/


/*Completed Start*/

.completed{
    opacity: 0.3;
}

.completed__title{
    margin-bottom: 28px;
    font-size: 18px;
    line-height: 27px;
}

/*Completed End*/
/* Указываем box sizing */
*,
*::before,
*::after {
    box-sizing: border-box;
}

/* Убираем внутренние отступы */
ul[class],
ol[class] {
    padding: 0;
}

/* Убираем внешние отступы */
body,
h1,
h2,
h3,
h4,
p,
ul[class],
ol[class],
li,
figure,
figcaption,
blockquote,
dl,
dd {
    margin: 0;
}

/* Выставляем основные настройки по-умолчанию для body */
body {
    min-height: 100vh;
    scroll-behavior: smooth;
    text-rendering: optimizeSpeed;
    line-height: 1.5;
}

/* Удаляем стандартную стилизацию для всех ul и il, у которых есть атрибут class*/
ul[class],
ol[class] {
    list-style: none;
}

/* Элементы a, у которых нет класса, сбрасываем до дефолтных стилей */
a:not([class]) {
    -webkit-text-decoration-skip: ink;
            text-decoration-skip-ink: auto;
}

/* Упрощаем работу с изображениями */
img {
    max-width: 100%;
    display: block;
}

/* Указываем понятную периодичность в потоке данных у article*/
article > * + * {
    margin-top: 1em;
}

/* Наследуем шрифты для инпутов и кнопок */
input,
button,
textarea,
select {
    font: inherit;
}

*{
    font-family: 'Poppins', sans-serif;
}

